.cover {
  height: calc(100vh + 150px);
  position: relative;
  overflow: hidden;
}
.cover:after {
    content: '';
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 150px 1920px;
    border-color: transparent transparent #fafafa transparent;
    position: absolute;
    left: 0;
    bottom: 0;
}
.cover .cover__content {
    position: relative;
    z-index: 2;
}
.cover .cover__content .content__info .info__first-name {
      font-size: 72px;
      line-height: 72px;
}
.cover .cover__content .content__info .info__last-name {
      font-size: 84px;
      line-height: 84px;
      font-weight: bold;
}
.cover .cover__content .content__info .info__job {
      color: #f1fa8c;
      font-size: 22px;
      line-height: 22px;
}

.animated-svg-icon {
  opacity: 0;
}
.animated-svg-icon.show {
    opacity: 1;
}
.animated-svg-icon.dark svg #Black path,
  .animated-svg-icon.dark svg #Black polygon {
    stroke: #ffffff;
}
.animated-svg-icon svg #Black path,
  .animated-svg-icon svg #Black polygon {
    stroke: #333333;
}
.animated-svg-icon #Frames-24px {
    display: none;
}

.mt-negative {
  position: relative;
  z-index: 2;
  margin-top: -80px;
}
.mt-negative.mt-negative--down {
    z-index: 1;
    padding-top: 60px;
}

.skills {
  padding: 150px 0;
  position: relative;
  overflow: hidden;
}
.skills:after, .skills:before {
    content: '';
    width: 0;
    height: 0;
    border-style: solid;
    position: absolute;
    left: 0;
}
.skills:before {
    border-width: 100px 1920px 0 0;
    border-color: #fafafa transparent transparent transparent;
    top: 0;
}
.skills:after {
    border-width: 0 0 100px 1920px;
    border-color: transparent transparent #fafafa transparent;
    bottom: 0;
}
